import React, { Component } from "react";
import { Input } from "antd";
const Search = Input.Search;
import { get, post } from "util/fetch";

import "./index.less";
export default class index extends Component {
  state = {
    kekeLink:
      "http://word.kekenet.com/index.php?do=recall&type=meaning&cid=492&coid=2",
    wordList: []
  };
  handleKekeLink = value => {
    console.log(value);
    let params = {
      url: value
    };
    post("/api/common/keke", params).then(res => {
      this.setState({
        wordList: res.data.wordList
      });
      log(res);
    });
  };
  handleSeachChange = e => {
    log(e.target);
    this.setState({
      kekeLink: e.target.value
    });
  };
  render() {
    const inputCell = (
      <div
        className="input-cell"
        style={{ marginTop: 10, backgroundColor: "#fff" }}
      >
        <h3 style={{ borderBottom: "1px solid #ddd", paddingLeft: 20 }}>
          输入参数
        </h3>
        <div className="content">
          <span>可可链接：</span>
          <Search
            placeholder="输入可可链接"
            enterButton="Search"
            // size="large"
            style={{ width: 650 }}
            value={this.state.kekeLink}
            onChange={this.handleSeachChange}
            onSearch={value => this.handleKekeLink(value)}
          />
        </div>
      </div>
    );
    const content = (
      <div className="table">
        <div className="row-line">
          <div className="cell">单词</div>
          <div className="cell">发音</div>
          <div className="cell">定义</div>
          <div className="cell">音标</div>
        </div>
        {this.state.wordList.map(item => (
          <div key={JSON.stringify(item)}>
            <div className="cell">{item.word}</div>
            <div className="cell">
              <img
                style={{ width: 30, cursor: "pointer" }}
                src="/images/sound.png"
                title={item.pronunciation_url}
                onClick={() => {
                  let phonetic_sound = new Audio(item.pronunciation_url);
                  phonetic_sound.play();
                }}
              />
            </div>
            <div className="cell">{item.definition}</div>
            <div className="cell">{item.phonetic_symbol}</div>
          </div>
        ))}
      </div>
    );
    return (
      <div className="keke-demo">
        {inputCell}
        {content}
      </div>
    );
  }
}
